import { Component, ViewChild } from '@angular/core';
import { AlertController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import { FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Form } from '../../../types';

@IonicPage()
@Component({
  selector: 'page-galeriaimg-create',
  templateUrl: 'galeriaimg-create.html',
})
export class GaleriaimgCreatePage {

  @ViewChild('fileInput') fileInput;
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  @ViewChild('portComponent2') portComponent2: IonicSelectableComponent;
  
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
 
  public imagen		     : any;

  public url		     : any;
  public estado		     : any;
  idempresa:any;
  idafiliado:any='na';


  private _ID                  : String;
  public foto             	: any='./assets/img/blanco.png';
  public pageTitle           	: string;
  public image            	: any='./assets/img/blanco.png';

  public categoria		     : any;

  public vcategoria  :any=[];

  isDisablednit :any;
  constructor( public alertCtrl: AlertController,public storage: Storage, private _IMAGE       : ImageProvider, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
        nombre     : ['', Validators.required],
      
        url     : [''],
        estado     : ['', Validators.required],
     
        imagen     : ['', Validators.required],
      
        categoria    :['', Validators.required]
     });


     this.authService.getReg('todos/categoria-imagen/' + this.navParams.data.idempresa,'frmcats').then((data) => {
      this.vcategoria=data;
     });  

   


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 


 

  this.portNameControl2 = this._FB.control(null, Validators.required);
 
  this.portForm2 = this._FB.group({
    portName: this.portNameControl2
  
  });


  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
     
       this.nombre		=	this.navParams.data.record.nombre;
     
     
       this.url		=	this.navParams.data.record.url;
     
       this.categoria		=	this.navParams.data.record.categoria;
    
       this.foto		=	this.navParams.data.record.imagen;
       this.image	=	this.navParams.data.record.imagen;
       this.imagen 	=	this.navParams.data.record.imagen;
       this.estado		=	this.navParams.data.record.estado;
       this.url		=	this.navParams.data.record.url;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  


  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = { 
       idempresa:this.navParams.data.idempresa,
     nombre : this.form.controls['nombre'].value,
    
     url : this.form.controls['url'].value,
      
     imagen: this.imagen,
     categoria : this.form.controls['categoria'].value._id,
   
     estado : this.form.controls['estado'].value,
    
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' galeria de imagenes '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'galeriaimgs').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'galeriaimgs').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  
  updateProfileImage() {
    if (Camera['installed']()) {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }
  else
  { this.fileInput.nativeElement.click();}
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
        this.imagen   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  
  selectImage() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.imagen  	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }   
  }
  
  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this._IMAGE.generateFromImage(this.image, 500, 500, 0.5, data => {
          this.image = data;
          this.imagen =  data;

         this.nombre= event.target.files[0].name
         
      });
   };
      reader.readAsDataURL(event.target.files[0]);
    }
}





//********************************************************************************************* */
port2: Form;
portForm2: FormGroup;
portNameControl2: FormControl;
  onAddForm2(event: {
    component: IonicSelectableComponent
  }) {
    this.portNameControl2.reset();
    this.portNameControl2.setValue(event.component.searchText);
    event.component.showAddItemTemplate();
  }
  
  onSaveForm2(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    this.portNameControl2.setValue(event.item.nombre);
    event.component.showAddItemTemplate();
  }
  



  deleteSelectedItem2(op) {
    let itemToDeleteIndex;

    this.vcategoria.forEach((selectedItem, itemIndex) => {
      if (selectedItem._id==op) {
        itemToDeleteIndex = itemIndex;
      }
    });

    this.vcategoria.splice(itemToDeleteIndex, 1);
  }

  onDeleteForm2(event: {
    component: IonicSelectableComponent,
    item: Form
  },op2) {
    let alert = this.alertCtrl.create({ title: 'Seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {   
          var aa:any;
          aa=event.item
          let recordID 		: string		= aa._id;
          this.authService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'frmcats').then((data) => {
            event.component.deleteItem({id:recordID,nombre:'aaa'});
            this.deleteSelectedItem2(recordID);
              
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }
  
  addForm2(op) {
    let creat='Crea';
     let   options       : any	     = { 
       idempresa: this.navParams.data.idempresa,
     idunidad : op,
     nombre : this.portNameControl2.value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' galeria imgs '}
    };
    if(options){
      this.authService.createReg('',options,'frmcats').then((result) => {

        var aa:any=result;
        let port = new Form({
          id: aa._id,
          nombre: this.portNameControl2.value
       
        });
        this.portComponent2.addItem(port).then(() => {
          this.portComponent2.search(port.nombre);
        });
        this.portNameControl2.reset();  
        this.portComponent2.hideAddItemTemplate();
    }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
    }



  }
  
  saveForm2(port: Form,op) {
    let creat='Actualiza';
    let   options       : any	     = { 
      idempresa:this.navParams.data.idempresa,
    idunidad : op,
    nombre : this.portNameControl2.value,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' galeriaimgs'}
   };
   if(options){
     var aa:any;
     aa=port;
     this.authService.createReg(aa._id,options,'frmcats').then((result) => {
       port.nombre = this.portNameControl2.value;
       this.portComponent2.hideAddItemTemplate();
   }, (err) => {  
     this.msj.showError3('Error',err.error);
   });
  }
}

}



